import topNavigationBar from "@/components/top-navigation-bar/top-navigation-bar.vue"
export default {
	components:{topNavigationBar},
	data() {
		return {
			current: 0,
			url:"q0022eew4t8",
			imgUrl: this.$imgUrl,
			title: '会员',
			info: [],
			functionList:[{
				text: '会员中心',
				imgUrl: '../../static/memberImg/functionList1.png'
			},{
				text: '最新消息',
				imgUrl: '../../static/memberImg/functionList2.png'
			},{
				text: '守护卡介绍',
				imgUrl: '../../static/memberImg/functionList3.png'
			},{
				text: '营养食品',
				imgUrl: '../../static/memberImg/functionList4.png'
			},{
				text: '常见问题',
				imgUrl: '../../static/memberImg/functionList5.png'
			}],
			novelty: [],
			clientFeedbackList: []
		}
	},
	onLoad:function(option){
		var that = this
		uni.getStorage({
		    key: 'memberInfo',
		    success: function (res) {
					that._getHomeBanner()
					that._getMembersLuckySchemeList()
					that._getFeedbackList()
		    },
			fail:function(res){
				uni.redirectTo({
					url: "../memberLogin/memberLogin"
				})
			}
		});
	},
	methods: {
		// 视频播放报错
		videoErrorCallback: function(e) {
			// uni.showModal({
			// 	content: e.target.errMsg,
			// 	showCancel: false
			// })
		},
		change (e) {
			this.current = e.detail.current;
		},
		// 获取banner图
		_getHomeBanner () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_home_banner').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.info = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 获取好运方案列表
		_getMembersLuckySchemeList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_members_lucky_scheme_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.novelty = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 获取客户反馈
		_getFeedbackList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_feedback_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.clientFeedbackList = res.data.data;
					//this.clientFeedbackList[i].url
					for(let i=0;i<this.clientFeedbackList.length;i++){
						if (this.clientFeedbackList[i].url) {
							this.clientFeedbackList[i].url = this.getCaption(this.clientFeedbackList[i].url)
						}
					}
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		skipFunctionList(text) {
			if (text == '会员中心') {
				uni.navigateTo({
					url: '../../pagesMember/MemberCentre/MemberCentre'
				})
			}
			if (text == '最新消息') {
				uni.navigateTo({
					url: '../../pagesMember/MemberLatestNews/MemberLatestNews'
				})
			}
			if (text == '守护卡介绍') {
				uni.navigateTo({
					url: '../../pagesMember/MemberGuardianCard/MemberGuardianCard'
				})
			}
			if (text == '营养食品') {
				uni.navigateTo({
					url: '../../pagesMember/MemberNutritiousFood/MemberNutritiousFood'
				})
			}
			if (text == '常见问题') {
				uni.navigateTo({
					url: '../../pagesMember/MemberCommonProblem/MemberCommonProblem'
				})
			}
		},
		toMemberGoodLuckPlan(item) {
			var items = JSON.stringify(item)
			uni.navigateTo({
				url: '../../pagesMember/MemberGoodLuckPlan/MemberGoodLuckPlan?item=' + items
			})
		},
		toMemberDeepButler() {
			uni.navigateTo({
				url: '../../pagesMember/MemberDeepButler/MemberDeepButler'
			})
		},
		getCaption(obj) {
		            var index = obj.lastIndexOf("vid=");
		            obj = obj.substring(index + 4, obj.length);
		            //  console.log(obj);
		            return obj;
		        }
	}
}
